// inspectFrom

import Vue from 'vue'

// 导入提示组件
import {Toast}  from 'vant'

// 注册提示组件
Vue.use(Toast);


// 检验注册信息
class InspectFrom{
    constructor(){}

    inspect(obj){
         
         for(let key in obj){
             let item = obj[key];
             if( !item.reg.test(item.value) ){
                    Toast(item.errorMsg);
                    return false;
             }
         }

         return true;       
    }
}


export const inspectFrom = new InspectFrom();

