<template>
    <div class="myFindack">
        <!-- 顶部导航 -->
        <van-nav-bar class="loginBar" :border='false'>
               <template #left>
                    <van-icon name="arrow-left" color="#727272" size="24px" @click="getBack"/>
               </template>
               <template #title>
                   <span class="title">
                          忘记密码
                   </span>
               </template>

               <template #right>
                   <div class="navigation">
                       <span class="iconfont icon-sangedian" ></span>
                       <span class="iconfont icon-tongxinyuan" @click="getPage('Home')"></span>
                   </div>
               </template>
        </van-nav-bar>

        <!-- logo图标 -->
        <div  class="iconfont icon-icon_ruixingkafei1 logins"></div>
        <p><span></span></p>

        <!-- 登录表单 -->
        <van-form  > 
            <!-- 输入框 -->
            <van-field
                v-model="newObj.phone"
                class="froms01"
                placeholder="邮箱"
            />
            <van-field
                v-model="newObj.password"
                class="froms02"
                type='text'
                placeholder="验证码"                
            >
            <template #button>
                <van-button size="small" :disabled='isDisabled' @click.stop="ff_enroll" type="primary">{{timeTet}}</van-button>
            </template>
            </van-field>

           <!-- 登录键 -->
            <div style="margin: 20px;">
                <van-button round block type="info" native-type="submit" color="#bfc9e2" @click.stop='ff_next'>
                下一步
                </van-button>
            </div>
        </van-form>
 
    </div>
</template>

<script>
    import '../assets/font/iconfont.css'
    import {inspectFrom} from '../assets/js/inspectFrom/inspectFrom'
    export default {
         name   : 'Findack',
         data() {
             return {
                     // 老用户登录
                    newObj:{
                        phone:null,
                        password:null,
                    }, 
                    eye01      : true,
                    time       : 60,
                    timeTet    : '发送验证码',
                    isDisabled : false,
                    
             }
         },
         methods:{
             getBack(){
                 this.$router.go(-1);
             },
              getPage(name){
                    this.$router.push({name});
            },
             // 发送验证码的方法
            ff_enroll(){
                let obj = {
                    phone:{
                        value    :  this.newObj.phone,
                        errorMsg :  '邮箱格式错误',
                        reg      :  /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
                    },     
                               
                }
            let isTest = inspectFrom.inspect(obj);

            if(isTest){
                    let email =  this.newObj.phone;
                    this.time = 60;
                    this.$toast.loading({
                                message: '处理中...',
                                // 禁止点击背景
                                forbidClick: true,
                                loadingType: 'spinner',
                    });


                    this.axios({
                        url:'/emailValidCode',
                        method:'POST',
                        data:{
                           appkey: this.appkey,
                           email
                        },
                        
                    })
                    .then(res => {
                        if(res.data.code == "J001"){
                            this.$toast('验证码已经发送至邮箱');
                            this.isDisabled = true;
                            let times = setInterval(()=>{
                                this.time = this.time - 1 ;
                                this.timeTet = `${this.time}秒，重新发送`;
                                
                                if(this.time < 0){
                                        this.timeTet    = '发送验证码';
                                        this.isDisabled = false
                                }
                            },1000)

                        }
                      
                    })
                    .catch(err =>{

                    })
            }

            },
            // 下一步
            ff_next(){
                   let obj = {
                    phone:{
                        value    :  this.newObj.phone,
                        errorMsg :  '邮箱格式错误',
                        reg      :  /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
                    },       
                    password:{
                        value    :  this.newObj.password,
                        errorMsg :  '验证码格式错误',
                        reg      :  /[0-9]/
                    },   
                    
                }
            let isTest = inspectFrom.inspect(obj);
            
            
          

            if(isTest){
                    this.$toast.loading({
                                message: '处理中...',
                                // 禁止点击背景
                                forbidClick: true,
                                loadingType: 'spinner',
                    });
         
                    this.axios({
                        url:"/checkValidCode",
                        method:'POST',
                        data: {
                            appkey    : this.appkey,
                            validCode : this.newObj.password
                        },
                    })
                    .then(res=>{
                        if(res.data.code == "K001"){
                            this.$toast(res.data.msg);
                            setTimeout(()=>{
                                this.$router.push({name:'Pass'})
                            },400)
                        }else{
                            this.$toast(res.data.msg);
                        }
                    })
                    .catch(err=>{
                        
                    })
            }

            }

                

         }
    }
</script>

<style lang="less" scoped>
.myFindack{
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;

    /deep/ .loginBar{
        background-color: #f5f5f5;
        font-weight: 550;


      
    }
    .logins{
        font-size: 230px;
        color:#4E6EB0;
    }
    .navigation{
         background-color: #fff;
         padding: 0px 6px;
         border-radius: 20px;
         position: relative;
         right: -9px;
         box-shadow: 1px 1px 8px 1px #ccc;
         opacity: 0.7;


         >span:nth-child(1)::after{
             content:  '';
             width: 2px;
             height: 20px;
             border-left: 1.5px solid #e8e8e8;
             position: absolute;
             top: 1px;
             left: 30px;
             
            
             
         }
         span{
             cursor: pointer;
             font-size: 22px;
             margin: 10px 8px;
             position: relative;
         }
    }
    .title{
        font-size: 17px;
    }

    /deep/ .imgs{
        margin-top: 20px;
    }

    /deep/.van-form{
        width: 95%;
        margin: 0px auto;
    }
    /deep/.van-button--primary{
        background-color: #4e6eb0;
        border: none;
        padding: 1px 5px;
        transform: scale(.9);
    }

     
}

</style>